.info-tool__container {
  max-width: 430px;
  background: #ffffff;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  text-align: center;
  padding: 60px 35px;
}

@media (max-width: 769px) {
  .info-tool__container {
    max-width: 280px;
    padding: 50px 0px;
  }
}
